import {Injectable} from '@angular/core';
import {Todo} from './todo';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {appConfig} from '../app.config';

@Injectable()
export class TodoDataService {

  private mockHost = '../../assets/mockData/';

  // Placeholder for last id so we can simulate
  // automatic incrementing of id's
  lastId = 0;

  // Placeholder for todo's
  todos: Todo[] = [];

  constructor(private http: HttpClient) {
  }

  addTodo(todo: Todo) {
    const currUser = JSON.parse(sessionStorage.getItem('currentUser'));
    this.http.post<any>(appConfig.apiUrl + '/addTodo', { username: currUser.username, todo: todo })
      .subscribe(response => {
        this.todos = [...response.todos];
      });
    return this;
  }

  // Simulate DELETE /todos/:_id
  deleteTodoById(_id: string): TodoDataService {
    const currUser = JSON.parse(sessionStorage.getItem('currentUser'));
    this.http.post<any>(appConfig.apiUrl + '/deleteTodo', { username: currUser.username, todo_id: _id })
      .subscribe(response => {
        this.todos = [...response.todos];
      });
    return this;
  }

  // Simulate PUT /todos/:_id
  updateTodoById(_id: string, values: Object = {}): Todo {
    const todo = this.getTodoById(_id);
    if (!todo) {
      return null;
    } if (false) {

    }
    Object.assign(todo, values);
    return todo;
  }

  initializeTodos(todos: Array<Todo>) {
    this.todos = [...todos];
  }

  getInitialTodos(currUser): Observable<Array<Todo>> {
    currUser = currUser;
    return this.http.get<any>(appConfig.apiUrl + '/getTodos/' + currUser.username);
  }

  getAllTodos(): Array<Todo> {
    return this.todos;
  }

  // Simulate GET /todos/:_id
  getTodoById(_id: string): Todo {
    return this.todos
      .filter(todo => todo._id === _id)
      .pop();
  }

  // Toggle todo complete
  toggleTodoComplete(todo: Todo) {
    const updatedTodo = this.updateTodoById(todo._id, {
      complete: !todo.complete
    });
    const currUser = JSON.parse(sessionStorage.getItem('currentUser'));
    this.http.post<any>(appConfig.apiUrl + '/toggleTodo', { username: currUser.username, todo: todo })
      .subscribe(response => {});
    return updatedTodo;
  }

}



